$primary: #845cff;
$white: #ffffff;
$black: #000000;
$text-color: #32325d;

@font-face {
  font-family: "Brown";
  src: url("fonts/Brown-Light.eot");
  src: local("Brown-Light"),
    url("fonts/Brown-Light.eot?#iefix") format("embedded-opentype"),
    url("fonts/Brown-Light.woff") format("woff"),
    url("fonts/Brown-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: "Brown";
  src: url("fonts/Brown-Regular.eot");
  src: local("Brown-Regular"),
    url("fonts/Brown-Regular.eot?#iefix") format("embedded-opentype"),
    url("fonts/Brown-Regular.woff") format("woff"),
    url("fonts/Brown-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Brown";
  src: url("fonts/Brown-Bold.eot");
  src: local("Brown-Bold"),
    url("fonts/Brown-Bold.eot?#iefix") format("embedded-opentype"),
    url("fonts/Brown-Bold.woff") format("woff"),
    url("fonts/Brown-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul,
ol {
  list-style: none;
}

img {
  max-width: 100%;
}

a {
  color: $black;
  text-decoration: none;
}

body {
  color: $black;
  overflow-x: hidden;
  font: normal 16px/1.2 "Brown";
}

input,
button,
textarea,
select {
  outline: none;
  font: normal 16px/1 "Brown";
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1265px;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.col-md-6 {
  width: 50%;
  flex: 0 0 50%;
  max-width: 50%;
}

.col-md-3 {
  width: 25%;
  flex: 0 0 25%;
  max-width: 25%;
}

.col-md-4 {
  width: 33.33%;
  flex: 0 0 33.33%;
  max-width: 33.33%;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;
  padding: 50px 0;
  transition: 0.5s ease all;
  .row {
    align-items: center;
    justify-content: space-between;
  }
  &.fixed {
    position: fixed;
    padding: 15px 0;
    background-color: $white;
    box-shadow: -6.691px 7.431px 20px 0px rgba(212, 212, 250, 0.2);
    animation: revealheader 0.5s ease forwards;
  }
}

@keyframes revealheader {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
}

.logo {
  a {
    display: block;
  }
}

.nav-toggle {
  display: none;
}

.navigation {
  ul {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    li {
      a {
        position: relative;
        font-size: 19px;
        font-weight: bold;
        color: $text-color;
        margin-right: 50px;
        display: inline-block;
        padding-bottom: 2px;
        &:after {
          position: absolute;
          content: "";
          bottom: -4px;
          left: 0;
          right: 0;
          width: 0;
          height: 2px;
          margin: 0 auto;
          transition: 0.25s ease all;
          background-color: $text-color;
        }
        &:hover {
          &:after {
            width: 100%;
          }
        }
        &.navigation__cta {
          color: $white;
          padding: 8px 20px 12px;
          &:after {
            content: none;
            display: none;
          }
        }
      }
      &:last-child {
        a {
          margin-right: 0;
        }
      }
    }
  }
  &__cta {
    border-radius: 4px;
    background-color: $primary;
    box-shadow: 0 1px 15px -5px rgba($black, 0.5);
  }
}

.hero {
  padding: 220px 0;
  background: url("../images/hero-bg.png") no-repeat top right;
  background-size: 90%;
}

.hero-content {
  width: 100%;
  max-width: 480px;
  margin-top: var(--header-height);
  &__h1 {
    font-size: 48px;
    line-height: 60px;
    color: $text-color;
    margin-bottom: 45px;
  }
  &__p {
    font-size: 23px;
    line-height: 32px;
    font-weight: lighter;
    color: lighten($text-color, 8%);
  }
  &__actions {
    display: flex;
    margin-top: 35px;
    align-items: center;
    a {
      font-size: 19px;
      color: $text-color;
      &:hover {
        text-decoration: underline;
      }
      &.hero-content__cta {
        color: $white;
        font-size: 15px;
        text-decoration: none;
      }
    }
  }
  &__cta {
    font-weight: bold;
    border-radius: 4px;
    padding: 12px 25px;
    margin-right: 28px;
    display: inline-block;
    text-transform: uppercase;
    background-color: $primary;
    box-shadow: 0 1px 15px -5px rgba($black, 0.5);
  }
}

.section-title {
  text-align: left;
  &__h2 {
    font-size: 48px;
    color: $text-color;
    font-weight: lighter;
    margin-bottom: 35px;
  }
  &__p {
    width: 100%;
    margin: 0 auto;
    font-size: 23px;
    max-width: 885px;
    line-height: 32px;
    font-weight: lighter;
    color: lighten($text-color, 8%);
  }
}

.top-features {
  position: relative;
  margin-top: -80px;
  &:before {
    content: "";
    position: absolute;
    top: -130px;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    background: url("../images/top-feature-bg.png") no-repeat top left;
  }
  &__left {
    padding-top: 220px;
    padding-right: 55px;
  }
  &__right {
    padding-left: 55px;
  }
  &__left,
  &__right {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .feature-block {
    padding: 60px 60px 90px;
    margin-bottom: 60px;
    border-radius: 10px;
    background-color: $white;
    box-shadow: -6.691px 7.431px 20px 0px rgba(212, 212, 250, 0.2);

    &:last-child {
      margin-bottom: 0;
    }
    &__h3 {
      font-size: 30px;
      color: darken($text-color, 15%);
      font-weight: lighter;
      margin: 45px 0 25px 0;
    }
    &__p {
      width: 100%;
      font-size: 18px;
      max-width: 445px;
      line-height: 24px;
      color: $text-color;
      font-weight: lighter;
    }
  }
}

.feature-section {
  padding: 195px 0 255px 0;
  background: url("../images/feature-bg.png") no-repeat bottom center;
  background-size: contain;
  .section-title {
    text-align: center;
  }
}

.feature-wrapper {
  .row {
    margin-top: 145px;
    & ~ .row {
      margin-top: 175px;
    }
  }
  .feature-item {
    padding: 0 10px;
    text-align: center;
    &__h3 {
      font-size: 20px;
      font-weight: normal;
      margin: 35px 0 15px 0;
      color: darken($text-color, 15%);
    }
    &__p {
      width: 100%;
      margin: 0 auto;
      font-size: 16px;
      max-width: 250px;
      line-height: 24px;
      font-weight: lighter;
      color: $text-color;
    }
  }
}

.get-started {
  padding-top: 190px;
  padding-bottom: 70px;
  &__left {
    padding-right: 70px;
  }
  &__feature {
    margin-top: 45px;
    .row {
      justify-content: space-between;
    }
  }
  .get-started-item {
    width: 100%;
    max-width: 200px;
    &__h3 {
      font-size: 30px;
      color: $text-color;
      font-weight: lighter;
      margin-bottom: 22px;
    }
    &__p {
      font-size: 16px;
      line-height: 24px;
      color: $text-color;
      font-weight: lighter;
    }
  }
}

.get-started-alt {
  padding-bottom: 210px;
  .get-started__right {
    padding-left: 60px;
  }
}

.more-features {
  padding-top: 80px;
  padding-bottom: 190px;
  background: url("../images/more-feature-bg.png") no-repeat top right;
  .section-title {
    margin-bottom: 160px;
  }
  .row {
    & + .row {
      margin-top: 100px;
    }
  }
}

.feature-list {
  &__title {
    font-size: 30px;
    line-height: 40px;
    color: $text-color;
    font-weight: normal;
    margin-bottom: 50px;
  }
  &__item {
    position: relative;
    font-size: 20px;
    color: $text-color;
    font-weight: lighter;
    margin-bottom: 32px;
    padding-left: 44px;
    &:before {
      position: absolute;
      content: "";
      width: 22px;
      height: 22px;
      top: 50%;
      left: 0;
      margin-top: 2px;
      border-radius: 100%;
      transform: translateY(-50%);
      border: 2px solid $primary;
    }
    &:after {
      position: absolute;
      content: "";
      top: 50%;
      left: 0;
      width: 15px;
      height: 5px;
      margin-top: -1px;
      margin-left: 6px;
      border-left: 2px solid $primary;
      border-bottom: 2px solid $primary;
      transform: translateY(-50%) rotate(-45deg);
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.subscribe-section {
  padding: 150px 0 120px 0;
  background-color: #fafaff;
  .section-title {
    &__h2 {
      margin-bottom: 25px;
    }
  }
  .row {
    align-items: center;
  }
}

.subscribe-box {
  padding-left: 55px;
  &__form {
    position: relative;
  }
  &__input {
    border: none;
    height: 80px;
    width: 100%;
    font-size: 23px;
    color: $text-color;
    border-radius: 5px;
    font-weight: lighter;
    padding: 0 160px 0 40px;
    background-color: $white;
    box-shadow: -6.691px 7.431px 20px 0px rgba(212, 212, 250, 0.2);
  }
  &__action {
    position: absolute;
    top: 50%;
    right: 40px;
    border: none;
    cursor: pointer;
    color: $primary;
    font-size: 15px;
    font-weight: bold;
    text-transform: uppercase;
    background-color: transparent;
    transform: translateY(-50%);
  }
}

.footer {
  background-color: darken($text-color, 9.5%);
  &__top {
    padding: 145px 0 35px 0;
  }
  &__bottom {
    padding: 98px 0;
    .row {
      justify-content: space-between;
    }
  }
  .footer-column {
    width: auto;
    flex: 1 1 auto;
    max-width: none;
    &:first-child {
      flex-grow: 3;
    }
  }

  .copyright {
    font-weight: lighter;
    color: lighten($text-color, 10%);
  }

  .privacy {
    display: flex;
    align-items: center;
    &__link {
      margin-right: 38px;
      font-weight: lighter;
      color: lighten($text-color, 10%);
      transition: 0.25s ease color;
      &:last-child {
        margin-right: 0;
      }
      &:hover {
        color: lighten($text-color, 30%);
      }
    }
  }
}

.footer-block {
  &__link {
    color: $white;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 26px;
    display: inline-block;
    font-weight: lighter;
    &:hover {
      text-decoration: underline;
    }
  }
  &__links {
    li {
      &:last-child {
        .footer-block__link {
          margin-bottom: 0;
        }
      }
    }
  }
  .footer-contact {
    .footer-block__link {
      margin-bottom: 0;
    }
  }
}

.footer-logo {
  > a {
    display: inline-block;
  }
}

.footer-contact {
  margin: 45px 0 40px 0;
  &__link-email {
    margin-top: 35px;
  }
}

.footer-social {
  display: flex;
  align-items: center;
  &__icon {
    display: inline-block;
    margin-right: 25px;
    &:last-child {
      margin-right: 0;
    }
  }
}

@media screen and (max-width: 1450px) {
  .hero {
    background-size: 100%;
  }
  .hero-content {
    margin-top: 20px;
  }

  .top-features {
    &__left {
      padding-right: 15px;
    }
    &__right {
      padding-left: 15px;
    }
  }
}

@media screen and (max-width: 1265px) {
  .container {
    padding: 0 20px;
  }
  .hero {
    padding: 160px 0;
    background-size: cover;
  }
  .top-features {
    &__left {
      padding-top: 120px;
    }
    .feature-block {
      padding: 40px 40px 60px;
    }
  }

  .feature-section {
    padding: 125px 0;
  }

  .feature-wrapper {
    margin-top: 95px;
    .row {
      & ~ .row {
        margin-top: 95px;
      }
    }
  }

  .get-started {
    padding-top: 90px;
    padding-bottom: 70px;
    &__left {
      padding-right: 15px;
    }
  }
  .get-started-alt {
    .get-started__right {
      padding-left: 15px;
    }
  }

  .more-features {
    padding-top: 40px;
    padding-bottom: 80px;
    .section-title {
      margin-bottom: 70px;
    }
    .row {
      & + .row {
        margin-top: 70px;
      }
    }
  }

  .subscribe-section {
    padding: 80px 0 90px;
  }

  .footer {
    &__top {
      padding: 75px 0 35px 0;
    }
    &__bottom {
      padding: 58px 0;
    }
  }
}

@media screen and (max-width: 991px) {
  .col-sm-6 {
    width: 50%;
    flex: 0 0 50%;
    max-width: 50%;
  }
  .col-sm-12 {
    width: 100%;
    flex: 0 0 100%;
    max-width: 100%;
  }

  .hero {
    background-size: 100%;
  }

  .navigation {
    ul {
      li {
        a {
          margin-right: 25px;
        }
      }
    }
  }

  .section-title {
    text-align: left;
  }

  .top-features {
    &__left {
      padding-top: 0;
    }
    .feature-block {
      padding: 40px;
      margin-top: 40px;
      margin-bottom: 0;
      &__p {
        max-width: 100%;
      }
    }
  }

  .feature-wrapper {
    .feature-item {
      margin-bottom: 50px;
    }
    .row {
      margin-top: 0;
      & ~ .row {
        margin-top: 0;
      }
    }
  }

  .feature-section {
    padding: 55px 0;
  }

  .get-started {
    padding: 50px 0;
    .container {
      > .row {
        flex-direction: column-reverse;
      }
    }
    &__inner {
      padding: 0;
      img {
        margin: 0 auto;
        display: block;
      }
    }
    &__left {
      padding: 0;
      margin-top: 30px;
    }
    &__right {
      padding: 0;
      margin-top: 30px;
    }
  }

  .get-started-alt {
    padding-top: 0;
    .container {
      > .row {
        flex-direction: column;
      }
    }
  }

  .more-features {
    background-position: center right;
    background-size: contain;
    .section-title {
      margin-bottom: 0;
    }
    .row {
      & + .row {
        margin-top: 0;
      }
    }
  }

  .feature-list {
    margin-top: 50px;
    &__title {
      margin-bottom: 20px;
    }
    &__item {
      margin-bottom: 20px;
    }
  }

  .subscribe-section {
    .section-title {
      text-align: center;
    }
  }
  .subscribe-box {
    padding: 0;
    margin-top: 30px;
  }

  .footer {
    .footer-column {
      width: 23.33%;
      flex: 0 0 23.33%;
      max-width: 23.33%;
      &:first-child {
        width: 30%;
        flex: 0 0 30%;
        max-width: 30%;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .header {
    padding: 20px 0;
    &.fixed {
      padding: 10px 0;
    }
  }
  .logo {
    img {
      max-height: 40px;
    }
  }
  .nav-toggle {
    position: absolute;
    display: block;
    top: 50%;
    right: 20px;
    width: 30px;
    height: 20px;
    margin-top: 10px;
    transform: translateY(-50%);
    span {
      position: relative;
      width: 100%;
      height: 2px;
      display: block;
      background-color: $text-color;
      &:before,
      &:after {
        position: absolute;
        content: "";
        top: -8px;
        left: -8px;
        right: 0;
        height: 2px;
        background-color: $text-color;
      }

      &:after {
        top: 8px;
        left: 10px;
      }
    }
    &.active {
      span {
        background-color: transparent;
        &:before, &:after {
          transition: 0.5s cubic-bezier(1, 0, 0, 1) transform;
        }
        &:before {
          top: 0;
          left: 0;
          transform: rotate(45deg);
        }
        &:after {
          top: 0;
          left: 0;
          transform: rotate(-45deg);
        }
      }
    }
  }
  .navigation {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    z-index: 9;
    padding: 20px;
    transform: translateY(-150%);
    background-color: $primary;
    transition: 0.5s cubic-bezier(1, 0, 0, 1) transform;
    &__active {
      transform: translateY(0);
    }
    ul {
      align-items: flex-start;
      flex-direction: column;
      li {
        margin-bottom: 15px;
        &:last-child {
          margin-bottom: 0;
        }
        a {
          color: $white;
          &:after {
            background-color: $white;
          }
          &.navigation__cta {
            color: $primary;
          }
        }
      }
    }
    &__cta {
      background-color: $white;
    }
  }
  .hero-content {
    &__h1 {
      font-size: 40px;
      line-height: 52px;
    }
    &__p {
      font-size: 20px;
      line-height: 29px;
    }
    &__actions {
      align-items: flex-start;
      flex-direction: column;
    }
    &__cta {
      margin-right: 0;
      margin-bottom: 15px;
    }
  }

  .section-title {
    &__h2 {
      font-size: 34px;
      margin-bottom: 15px;
    }
    &__p {
      font-size: 20px;
      line-height: 29px;
    }
  }

  .top-features {
    .feature-block {
      padding: 20px;
    }
  }

  .get-started {
    .get-started-item {
      max-width: 100%;
      margin-top: 20px;
      &:first-child {
        margin-top: 0;
      }
      &__h3 {
        font-size: 25px;
      }
    }
  }

  .more-features {
    padding-top: 0;
    background: none;
  }

  .feature-list {
    &__title {
      font-size: 25px;
      line-height: 30px;
    }
    &__item {
      font-size: 16px;
    }
  }
  .subscribe-section {
    padding: 40px 0 50px;
  }

  .subscribe-box {
    &__input {
      font-size: 16px;
      padding: 0 120px 0 15px;
    }
    &__action {
      right: 15px;
    }
  }

  .footer {
    &__top {
      padding: 35px 0 0;
    }
    &__bottom {
      padding: 0 0 30px 0;
      .row {
        flex-direction: column-reverse;
      }
    }
    .privacy {
      margin-bottom: 20px;
      &__link {
        margin-bottom: 10px;
      }
    }
    .footer-column {
      width: 100%;
      flex: 0 0 100%;
      max-width: 100%;
    }
    .footer-contact {
      margin: 20px 0 10px 0;
      .footer-block__link {
        margin-top: 0;
        margin-bottom: 10px;
      }
    }
    .footer-block {
      margin-bottom: 30px;
      &__link {
        margin-bottom: 10px;
      }
    }
  }

  .footer-social {
    &__icon {
      width: 20px;
      flex: 0 0 20px;
    }
  }
}

@media screen and (max-width: 500px) {
  .section-title {
    &__h2 {
      font-size: 30px;
    }
  }
  .col-sm-6 {
    width: 100%;
    flex: 0 0 100%;
    max-width: 100%;
  }
}
